export function floatLeft() {
  return `
    float: left !important;
  `;
}

export function floatRight() {
  return `
    float: right !important;
  `;
}

export function floatNone() {
  return `
    float: none !important;
  `;
}

export default {
  floatLeft,
  floatRight,
  floatNone,
};



